import type { ICommonComponentProps } from '../interface';
import { IPopoverProps } from '../../../../display/popover/Popover';

export interface ISplitButtonProps extends ICommonComponentProps, Pick<IPopoverProps, 'placement' | 'getContainer'> {
  icon: React.ReactElement;
  /**
   * 弹层节点
   */
  overlay: React.ReactElement | null;
  /**
   * 是否默认收起
   */
  autoClose?: boolean;
  /**
   * 弹层可见性
   */
  overlayVisible?: boolean;
  /**
   * 弹层可见性变化处理函数
   */
  onVisibleChange?: (e: boolean) => void;
  /**
   * 弹层样式
   */
  overlayStyle?: React.CSSProperties;

  onClick?: () => void;
  badgeDot?: boolean | { top: number; right: number }
  active?: boolean;
  width?: number;
}

export interface IBaseSplitButtonProps extends ISplitButtonProps {
  // id: string;
  size: 'large' | 'normal' | 'small';
}
